<template>
	<div class="green-certification">
		<box-item title="绿色认证">
			<div class="wrapper">
				<div class="item">
					<div class="left-icon one"></div>
					<div class="right-info">
						<p class="title">养殖类认证率</p>
						<p class="content">{{ breedCertifiRate }}%</p>
					</div>
				</div>
				<div class="item">
					<div class="left-icon two"></div>
					<div class="right-info">
						<p class="title">养殖类认证面积</p>
						<p class="content">{{ breedCertifiLandArea }}<span>万亩</span></p>
					</div>
				</div>
				<div class="item">
					<div class="left-icon three"></div>
					<div class="right-info">
						<p class="title">养殖类认证主体数</p>
						<p class="content">{{ breedCertifiCompany }}<span>家</span></p>
					</div>
				</div>
			</div>
		</box-item>
	</div>
</template>
<script>
import BoxItem from '@/components/common/BoxItem';
import {areaLevelCorAreaId} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			breedCertifiCompany: 0,
			breedCertifiLandArea: 0,
			breedCertifiRate: 0
		};
	},
	mounted() {
		this.initMoudle();
	},
	methods: {
		initMoudle() {
			this.getAllCateByArea();
		},
		getAllCateByArea() {
			this.$ajax
				.post(this.$api.safeSupervise.allCateByArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						this.breedCertifiCompany = data.obj.breedCertifiCompany;
						this.breedCertifiLandArea = data.obj.breedCertifiLandArea;
						this.breedCertifiRate = data.obj.breedCertifiRate;
					}
				})
				.catch(e => {
					console.log('渔业，绿色认证 error: ', e);
				});
		}
	},
	components: {
		BoxItem
	}
};
</script>
<style lang="scss" scoped>
.green-certification {
	height: 56%;
	box-sizing: border-box;
	// background-color: rgba(4, 41, 64, 0.6);
	.wrapper {
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		// justify-content: space-around;
		justify-content: center;
		align-items: center;
		//padding-bottom: 10px;
	}
	.item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 83%;
		height: 26%;
		max-height: 109px;
		border: 1px solid #10a4a9;
		border-radius: 55px;
		margin-bottom: 5%;
		background: #0a2033;
		box-shadow: 0px 0px 30px 0px rgba(2, 196, 252, 0.45) inset;
		&:nth-child(1) {
			margin-top: 5%;
		}
		.left-icon {
			width: 80px;
			height: 80px;
			margin-left: 14px;
			&.one {
				background: url('../../../assets/images/fisheries/green-certification-item1.png') no-repeat;
				background-size: contain;
			}
			&.two {
				background: url('../../../assets/images/fisheries/green-certification-item2.png') no-repeat;
				background-size: contain;
			}
			&.three {
				background: url('../../../assets/images/fisheries/green-certification-item3.png') no-repeat;
				background-size: contain;
			}
		}
		.right-info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			flex: 1;
			margin-left: 10%;
			.title {
				font-size: 16px;
				font-weight: bold;
				color: #9dd1d7;
				line-height: 14px;
			}
			.content {
				font-size: 30px;
				font-weight: bold;
				color: #feeb48;
				line-height: 19px;
				margin-top: 15px;
				span {
					font-size: 16px;
				}
			}
		}
	}
}
@media screen and (min-width: 3000px) {
	.green-certification {
		.item {
			width: 75%;
			height: 32%;
			border-radius: 60px;
			margin-top: 1.5%;
			margin-bottom: 1.5%;
			&:nth-child(1) {
				margin-top: 2%;
			}
			.right-info {
				.title {
					font-size: 20px;
					line-height: 20px;
					margin-top: 2px;
				}
				.content {
					font-size: 30px;
					line-height: 30px;
					margin-top: 10px;
					span {
						font-size: 24px;
					}
				}
			}
			.right-info {
				margin-left: 11%;
			}
		}
	}
}
</style>



